<template>
  <v-container grid-list-md>
    <div v-if="ActiveUser.Previously_Rented.length > 0">
    <v-layout row wrap>
      <v-flex xs3 v-for="movie in ActiveUser.Previously_Rented" :key="movie.Id">
        <v-container>
          <movie-tile :movie="movie"></movie-tile>
        </v-container>
      </v-flex>
    </v-layout>
    <v-fab-transition>
      <v-btn color="red darken-2" large fab fixed bottom right>
        <v-icon>shopping_cart</v-icon>
      </v-btn>
    </v-fab-transition>
    </div>
    <div v-else>
      Nothing in Renting History
    </div>
  </v-container>
</template>

<script>
  import Model from '../Model'
  import MovieTile from "./MovieTile";

  export default {
    components: {
      MovieTile,
    },
    props: {
      ActiveUser: Object
    },
    data() {
      return {
        subColor: "#ab302f"
      };
    },
    methods: {
    }
  };
</script>

<style scoped>
  #form {
    margin-right: 5%;
    margin-left: 5%;
    padding-top:10px;
    padding-bottom:30px;
    text-align: left;

  }

  .v-btn--large{
    padding: 10px !important;
  }

  .header {
    background-color: rgba(185, 134, 12, 0.79);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



