<template>
  <v-layout>
      <v-card color="red darken-4" width="400px;">
        <v-img
          :src="movie.Poster"
          height="300"
        ></v-img>

        <v-card-title primary-title>
          <div class="mb-3">
            <h3>{{movie.Name}} ({{movie.Year}})</h3>
            <small>Price: ${{movie.Price}}</small>
          </div>

          <v-layout row>
            <div v-for="i in movie.Rating">
              <v-icon color="yellow">star_border</v-icon>
            </div>
            <div v-for="i in (5-movie.Rating)">
              <v-icon>star_border</v-icon>
            </div>
          </v-layout>

        </v-card-title>

        <v-card-actions>
          <v-btn @click="$router.push('/movie/'+movie['.key'])" flat small color="dark"><v-icon>info</v-icon></v-btn>
          <v-btn @click="sendMovieClick();" :color="color" small left flat><v-icon>shopping_cart</v-icon></v-btn>
        </v-card-actions>
      </v-card>
  </v-layout>
</template>

<script>
    export default {
        name: "MovieTile",
        data() {
          return {
            color: "dark"
          }
        },
        props:{
          movie: Object,
        },
      methods:{
          sendMovieClick(m){
            var col = this.color === 'dark' ? "green lighten-2" : "dark";
            this.color = col;
           this.$parent.$emit('movie_added',this.movie)
          },
      }
    }

</script>

<style scoped>

</style>



