import Router from 'vue-router'
import MainCard from '../components/HomeCard.vue'
import RegCard from '../components/RegisterCard.vue'
import LoginCard from '../components/LoginCard.vue'
import Movies from '../components/Movies'
import MovieCard from '../components/MovieCard'
import HistoryCard from '../components/HistoryCard'
import AddMovie from '../components/AddMovie'
import UserCard from '../components/UserCard'
import CheckoutCard from '../components/CheckoutCard'
import NotFoundCard from '../components/404Card'
import TestCard from '../components/PayCard'

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: LoginCard,
    },
    {
      path: '/pay',
      name: 'pay',
      component: TestCard,
    },
    {
      path: '/register',
      name: 'register',
      component: RegCard,
    },
    {
      path: '/home',
      name: 'home',
      component: MainCard,
    },
    {
      path: '/movies',
      name: 'movies',
      component: Movies,
    },
    {
      path: "/movie/:selectedId",
      component: MovieCard,
      props: true
    },
    {
      path: "/history",
      component: HistoryCard,
      props: true
    },
    {
      path: "/addMovie",
      name: 'addMovie',
      component: AddMovie,
      props: true
    },
    {
      path: "/usersettings",
      name: 'usersettings',
      component: UserCard,
      props: true
    },{
      path: "/checkout",
      name: 'checkout',
      component: CheckoutCard,
      props: true
    },{
      path: "*",
      component: NotFoundCard,
    }
  ],
  mode: 'history',
});

export default router



